import * as React from "react";
import styled from "@emotion/styled";

interface Item {
  id: string;
  color: string;
  category: string;
  label: string;
  value: string;
}
interface Props {
  items: Item[];
}

const ListItem = () => {
  return <p>list item</p>;
};

const ListContainer = styled.div({
  display: "flex",
  flexDirection: "row",
});

const List: React.FC<Props> = ({ items }) => {
  return (
    <ListContainer>
      {items.map(() => {
        return <ListItem />;
      })}
    </ListContainer>
  );
};

export default List;
